/* 그리드 메인 외곽 DIV */
.aui-grid {
  font-family: "Malgun Gothic", "맑은 고딕", AppleSDGothicNeo-Light, sans-serif;
  font-size: 12px;
  line-height: 1.2em;
  border: 1px solid #cccccc;
  background: #ffffff;
  box-sizing: content-box;
  appearance: none;
  -webkit-appearance: none;
  outline: none;
  -webkit-touch-callout: none;
}

/* 그리드 table, tr, td 기본 - 셀 기본 */
.aui-grid .aui-grid-table,
.aui-grid .aui-grid-table tr,
.aui-grid .aui-grid-table td {
  padding: 0 !important;
  margin: 0 !important;
  border-spacing: 0 !important;
  border-collapse: separate !important;
  box-sizing: border-box !important;
  font-size: 12px;
  line-height: 1.5em;
}

/* 편집 모드 기본 텍스트 인풋  */
.aui-grid .aui-textinputer {
  font-size: 12px;
  border: 2px solid #2e6da4;
  outline: none;
  box-sizing: border-box !important;
  background: #ffffff !important;
  transition: none !important;
}

.aui-grid .aui-textinputer::-ms-clear {
  display: none;
}

/* label */
.aui-label {
  display: inline-block !important;
  font-weight: normal !important;
  margin: 0 !important;
  padding: 0 !important;
}

/* 체크박스, 라디오  */
.aui-checkbox,
.aui-radio {
  vertical-align: middle;
  margin: 0;
  margin-top: 0 !important;
  padding: 0;
}

/* 체크박스 */
.aui-checkbox {
  cursor: pointer;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  outline: 0;
  width: 20px !important;
  height: 20px !important;
  border: 0;
  background-image: url("../assets/images/checkbox_nomal.png");
  background-position: center;
  background-repeat: no-repeat;
}

.aui-checkbox:indeterminate:after {
  display: none;
}

.aui-checkbox:checked {
  background-image: url("../assets/images/checkbox_active.png");
}

.aui-checkbox:disabled {
  background-image: url("../assets/images/checkbox_disabled.png");
  cursor: not-allowed;
}

.aui-checkbox:after {
  display: none;
}

.aui-checkbox:checked:after {
  display: block;
}

.aui-checkbox:disabled:after {
  border-color: #555555;
}

/* 이미지  */
.aui-grid .aui-img {
  box-sizing: content-box;
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
}

/* 그리드 메세지 출력 레이어 */
.aui-grid-info-layer {
  background: #fff;
  font-size: 20px;
  font-weight: bold;
}

/** 그리드 유효성 검사 툴팁 레이어 */
.aui-grid-validator-message-layer {
  color: #fff;
  padding: 2px 6px;
  border: 1px solid #999;
  background: #ff0000;
  background: -webkit-linear-gradient(top, #ff6c6c, #c90000);
  background: -moz-linear-gradient(top, #ff6c6c, #c90000);
  background: -ms-linear-gradient(top, #ff6c6c, #c90000);
  background: -o-linear-gradient(top, #ff6c6c, #c90000);
  background: linear-gradient(to bottom, #ff6c6c, #c90000);
  border-radius: 4px;
  line-height: 1.5em;
  z-index: 1065;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.3);
  -moz-box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.3);
}

/* 노데이터 메세지 레이어*/
.aui-grid-nodata-msg-layer {
  background: url(./images/info.png) 0 50% no-repeat;
  position: absolute;
  padding: 44px 0 0 80px;
  margin: 0 auto;
  width: auto;
  height: 72px;
  box-sizing: content-box;
}

/* 그리드 수평, 수직 라인 만들기 - 바디 */
