  -ms-touch-action: none;
}

.aui-hscrollbar .aui-scroll-thumb:hover {
  background: #bbbbbb;
}

.aui-vscrollbar .aui-scroll-thumb {
  box-sizing: border-box;
  background: #dddddd;
  border: 1px solid #cccccc;
  touch-action: none;
  -ms-touch-action: none;
}

.aui-vscrollbar .aui-scroll-thumb:hover {
